.wooconvo-img-editor-main {
  width: 500px;
  margin: 10px auto;
}

.wooconvo-editor {
  font-family: sans-serif;
  text-align: center;
}

#wooconvo-canvases {
  position: relative;
  width: 350px;
  height: 350px;
  /* text-align: center; */
  margin: 0 auto;
}

.wooconvo-canvas {
  width: 350px;
  height: 350px;
  border: 1px solid #bdc3c7;
  margin: 5px auto;
}

.wooconvo-canvas canvas,
.canvas-container {
  position: absolute !important;
  left: 0 !important;
  top: 0 !important;
  width: 100% !important;
  height: 100% !important;
}

#cursor {
  pointer-events: none !important;
}

.wooconvo-images {
  width: 100%;
  display: flex;
}

.wooconvo-images img {
  width: 100px;
}
.wooconvo-images div {
  flex: 1;
  text-align: center;
  cursor: pointer;
}

.wooconvo-editor-btn {
  padding: 5px;
  background-color: black;
  color: #fff;
  margin: 3px;
}

.wooconvo-image-ready {
  text-align: center;
}
